@use "variables" as var;

@mixin telefono {
	@media (min-width: var.$telefono) {
		// uso de bloque de código
		// en poca palabras lo que esta dentro de los  { }
		@content;
	}
}

@mixin tablet {
	@media (min-width: var.$tablet) {
		@content;
	}
}

@mixin desktop {
	@media (min-width: var.$desktop) {
		@content;
	}
}

@mixin desktop {
	@media (min-width: var.$desktopXL) {
		@content;
	}
}

@mixin grid($columnas, $espaciado) {
	display: grid;
	grid-template-columns: repeat($columnas, 1fr);
	gap: $espaciado;
}

@mixin resetear-lista {
	margin: 0;
	padding: 0;
	list-style: none;
}
